import React, { useState, useEffect, useCallback } from 'react';
import debounce from 'lodash.debounce';
import { alert, info } from '@pnotify/core';
import '@pnotify/core/dist/PNotify.css';
import '@pnotify/core/dist/BrightTheme.css';

export default function CountrySearch() {
  const [query, setQuery] = useState('');
  const [countries, setCountries] = useState([]);

  const fetchCountries = async (name) => {
    if (!name) return;

    try {
      const response = await fetch(`https://restcountries.com/v2/name/${name}`);
      if (!response.ok) throw new Error('No countries found');
      const data = await response.json();
      handleCountries(data);
    } catch (error) {
      alert({ text: 'No matching countries found!' });
      setCountries([]);
    }
  };

  const handleCountries = (data) => {
    if (data.length > 10) {
      info({ text: 'Too many matches found. Please enter a more specific query.' });
      setCountries([]);
    } else {
      setCountries(data);
    }
  };

  const debouncedFetch = useCallback(debounce(fetchCountries, 500), []);

  useEffect(() => {
    debouncedFetch(query);
  }, [query, debouncedFetch]);

  return (
    <div className="p-4 max-w-md mx-auto">
      <input
        type="text"
        placeholder="Enter country name"
        className="w-full p-2 border rounded"
        value={query}
        onChange={(e) => setQuery(e.target.value)}
      />

      <div className="mt-4">
        {countries.length === 1 && (
          <CountryDetails country={countries[0]} />
        )}

        {countries.length > 1 && countries.length <= 10 && (
          <ul className="list-disc list-inside">
            {countries.map((country) => (
              <li key={country.alpha3Code}>{country.name}</li>
            ))}
          </ul>
        )}
      </div>
    </div>
  );
}

function CountryDetails({ country }) {
  return (
    <div className="p-4 border rounded shadow">
      <h2 className="text-xl font-bold mb-2">{country.name}</h2>
      <p><strong>Capital:</strong> {country.capital}</p>
      <p><strong>Population:</strong> {country.population.toLocaleString()}</p>
      <p><strong>Languages:</strong> {country.languages.map(l => l.name).join(', ')}</p>
      <img src={country.flag} alt={`Flag of ${country.name}`} className="mt-2 w-32" />
    </div>
  );
}
